<template>
  <TopBar ref="myRef"></TopBar>
  <el-table :data="data" style="width: 100%">
    <!--    <el-table-column prop="date" label="Date" width="180" />-->
    <el-table-column prop="playlistTag.name" label="Name"/>
    <el-table-column prop="type" label="Name"/>
    <el-table-column prop="createTime" label="createTime"/>
    <el-table-column prop="usedCount" label="usedCount"/>
  </el-table>
  <el-row class="mb-2">
    <el-button @click="push">中文</el-button>
    <el-button><a href="http://localhost:8000/index">123</a></el-button>
  </el-row>

</template>
<script>
import {get} from "@/api/myRequest";
import {ref} from "vue";
import {useRouter} from 'vue-router'
import TopBar from "@/components/TopBar";

const data = ref([])
const myRef = ref()
get('/api/hello').then(value => data.value = value.tags)

export default {
  name: "MyEcharts",
  components: {TopBar},
  setup() {
    const route = useRouter()
    const push = () => {

      route.push('http://localhost/api/index')
    }
    return {data, push, myRef}
  }
}
</script>

